@import '../../styles/_colors';
@import '../../styles/_fonts';

.nav{
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;
    transition: top 300ms;
    background: $lila;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &__logo{
        display: flex;
        flex-direction: row;
        align-items: center;

        .burger{
            display: none;
        }

        img{
        
            width: 5rem;
            padding: 2rem;
        }
    } 
    &__menu{
        display: flex;
        flex-direction: row;

        list-style: none;
        a{
            text-decoration: none;
            color: white;
            padding: 0 2rem;
        }
    }
    &__social{
    display: block;
    a img{
        width: 2.5rem;
        padding: 0 1rem;
        padding-right: 2rem;
    }

    }
    
}

@media (max-width: 1100px){
    .nav{

        
        &__logo{
            .burger{
                
                .burger-open, .burger-close{
                position: absolute;
                top: 30px;
                right: 30px;

                }
            }
        } 


        &__menu{
            display: none;
        }
        &__social{
            display: none;
        }
    



    }
}
